import React from 'react';
import { NavLink } from 'react-router-dom';
const Navbar = () => {
    return (
        <>
          <div className="bg-light">
          <div className="col-lg-10 mx-auto " >
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                    <div className="container-fluid">
                        <NavLink className="navbar-brand" to="/">React Project</NavLink>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse " id="navbarSupportedContent">
                            <ul className="navbar-nav ml-auto
                            ">
                                <li className="nav-item">
                                    <NavLink className="nav-link  " exact activeClassName="menu_link"  aria-current="page" to="/">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link"  activeClassName="menu_link" to="About">About</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link " activeClassName="menu_link"  aria-current="page" to="/Services">Service</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink className="nav-link" activeClassName="menu_link"  to="/Contact">Contact</NavLink>
                                </li>
                               
                                
                            </ul>
                             
                        </div>
                    </div>
                </nav>  
            </div>
          </div>
            
        </>
    )

}
export default Navbar;